.ty-alert {
  border: 1px solid;
  border-radius: 3px;
  box-sizing: border-box;
  padding: 10px 30px 10px 13px;
  position: relative;
  font-weight: 400;
  opacity: 1;
  font-size: 1rem;
  transition: all 300ms;
  transform-origin: center top;
  overflow: hidden;
  display: flex;
  align-items: flex-start;
}
.ty-alert + .ty-alert {
  margin-top: 14px;
}
.ty-alert__title {
  box-sizing: border-box;
  margin: 0 0 5px;
  font-size: 18px;
  font-weight: 500;
}
.ty-alert__desc {
  box-sizing: border-box;
  line-height: 16px;
}
.ty-alert__icon {
  line-height: 16px;
  margin-right: 7px;
  position: relative;
  top: 1px;
}
.ty-alert__close-btn {
  position: absolute;
  top: 10px;
  right: 13px;
  cursor: pointer;
  float: right;
  user-select: none;
}
.ty-alert_error {
  border-color: #ffa39e;
  background-color: #fff1f0;
  color: #f3382a;
}
.ty-alert_warning {
  border-color: #ffe58f;
  background-color: #fffbe6;
  color: #f29000;
}
.ty-alert_success {
  border-color: #b7eb8f;
  background-color: #f6ffed;
  color: #4db919;
}
.ty-alert_info {
  border-color: #91d5ff;
  background-color: #e6f7ff;
  color: #0b8aff;
}

.ty-anchor {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  list-style: none;
  position: relative;
  background-color: #fff;
}
.ty-anchor__ink {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
}
.ty-anchor__ink:before {
  content: "";
  position: relative;
  display: block;
  width: 2px;
  height: 100%;
  margin: 0 auto;
  background-color: #f0f0f0;
}
.ty-anchor__ink-ball {
  position: absolute;
  left: 50%;
  width: 8px;
  height: 8px;
  background-color: #fff;
  border: 2px solid #6e41bf;
  border-radius: 8px;
  transform: translateX(-50%);
  transition: top 0.3s ease-in-out;
}
.ty-anchor__link {
  padding: 7px 0 7px 16px;
}
.ty-anchor__link .ty-anchor {
  border: 0;
}
.ty-anchor__link_active > a {
  color: #6e41bf;
}
.ty-anchor__link-title {
  display: block;
  overflow: hidden;
  color: rgba(0, 0, 0, 0.65);
  white-space: nowrap;
  text-overflow: ellipsis;
  border-left: 2px solid transparent;
}
.ty-anchor__link-title:hover {
  color: #6e41bf;
  text-decoration: none;
}
.ty-anchor__link-title:not(:only-child) {
  margin-bottom: 5px;
}

.ty-aspect-ratio {
  box-sizing: border-box;
  position: relative;
  display: inline-block;
}
.ty-aspect-ratio__padding {
  display: block;
  height: 0;
}
.ty-aspect-ratio__inside {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}

.ty-avatar {
  box-sizing: border-box;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: #ccc;
  color: #fff;
  white-space: nowrap;
  position: relative;
  vertical-align: middle;
  user-select: none;
  border: 2px solid #fff;
}
.ty-avatar__img {
  width: 100%;
  height: 100%;
}
.ty-avatar__text {
  position: absolute;
  left: 50%;
  transform-origin: 0 center;
  transform: translateX(-50%);
}
.ty-avatar__presence {
  position: absolute;
  bottom: 0;
  right: 0;
  border-radius: 50%;
  box-shadow: 0 0 0 0.1rem #fff;
  height: 0.4em;
  width: 0.4em;
}
.ty-avatar__presence_online {
  background-color: #52c41a;
}
.ty-avatar__presence_busy {
  background-color: #f44336;
}
.ty-avatar__presence_away {
  background-color: #ff9800;
}
.ty-avatar__presence_offline {
  background-color: #ced4da;
}
.ty-avatar_circle {
  border-radius: 50%;
}
.ty-avatar_circle .ty-avatar__img {
  border-radius: 50%;
}
.ty-avatar_square {
  border-radius: 2px;
}
.ty-avatar_clickable {
  cursor: pointer;
}
.ty-avatar-group .ty-avatar {
  z-index: 1;
}
.ty-avatar-group .ty-avatar:hover {
  z-index: 2;
}

.ty-back-top {
  position: fixed;
  right: 40px;
  bottom: 30px;
  background-color: rgba(0, 0, 0, 0.3);
  width: 45px;
  height: 45px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: opacity 200ms linear 0s, visibility;
}

