.ty-badge {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  line-height: 1;
}
.ty-badge__count {
  box-sizing: border-box;
  display: inline-block;
  z-index: 10;
  position: absolute;
  transform: translateX(-50%) translateY(-50%);
  border-radius: 18px;
  box-shadow: 0 0 0 1.5px #fff;
  min-width: 18px;
  min-height: 18px;
  line-height: 18px;
  padding: 0 5px;
  color: #fff;
  font-weight: normal;
  font-size: 12px;
  white-space: nowrap;
  text-align: center;
}
.ty-badge__custom {
  position: absolute;
  right: 0;
  top: 0;
  transform: translateX(50%) translateY(-50%);
}
.ty-badge__dot {
  box-sizing: border-box;
  display: inline-block;
  z-index: 10;
  position: absolute;
  transform: translateX(-50%) translateY(-50%);
  border-radius: 18px;
  box-shadow: 0 0 0 1.5px #fff;
  width: 6px;
  height: 6px;
  line-height: 6px;
}
.ty-badge__dot_wave:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: inherit;
  animation: ty-processing 1.2s infinite ease-in-out;
}
.ty-badge_no-wrap .ty-badge__count,
.ty-badge_no-wrap .ty-badge__dot {
  display: block;
  transform: none;
  position: relative;
}

