  -moz-osx-font-smoothing: grayscale;
}
.ty-icon_spin:before {
  display: inline-block;
  animation: ty-rotate 1s linear infinite;
}

.ty-image {
  box-sizing: border-box;
}
.ty-image_round {
  border-radius: 50%;
}

.ty-input {
  position: relative;
  box-sizing: border-box;
  color: #32325d;
}
.ty-input__input {
  box-sizing: border-box;
  width: 100%;
  margin: 0;
  color: #32325d;
  border: 1px solid #d9d9d9;
  transition: all 0.3s;
  outline: 0;
  border-radius: 2px;
  font-size: 1rem;
}
.ty-input__input:hover {
  border-color: #6e41bf;
}
.ty-input__input:focus {
  border-color: rgba(110, 65, 191, 0.8);
  box-shadow: 0 0 0 2px rgba(110, 65, 191, 0.2);
}
.ty-input__input::placeholder {
  color: #bfbfbf;
}
.ty-input__prefix, .ty-input__suffix {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  margin: 0 8px;
}
.ty-input__prefix {
  left: 0;
}
.ty-input__suffix {
  right: 0;
}
.ty-input__clear-btn {
  display: inline-block;
  color: rgba(0, 0, 0, 0.25);
  width: 14px;
  height: 14px;
  position: relative;
  top: 2px;
  cursor: pointer;
}
.ty-input_sm .ty-input__input {
  font-size: 0.875rem;
  height: 24px;
  line-height: 24px;
}
.ty-input_sm .ty-input__clear-btn {
  transform: scale(0.7);
}
.ty-input_md .ty-input__input {
  font-size: 1rem;
  height: 32px;
  line-height: 32px;
}
.ty-input_md .ty-input__clear-btn {
  transform: scale(0.8);
}
.ty-input_lg .ty-input__input {
  font-size: 1.25rem;
  height: 42px;
  line-height: 42px;
}
.ty-input_disabled .ty-input__input {
  cursor: not-allowed;
  background-color: #f4f4f5;
  color: #999;
}
.ty-input_disabled .ty-input__input:hover {
  border-color: #d9d9d9;
}

