.ty-layout-footer {
  box-sizing: border-box;
  padding: 24px 50px;
  background-color: #fff;
}

.ty-layout-content {
  flex: 1;
  box-sizing: border-box;
  background-color: #fff;
}

.ty-layout-sidebar {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  transition: all 200ms;
  background: #12131a;
