.ty-typography mark {
  padding: 0;
  background-color: #ffe58f;
}
.ty-typography u {
  text-decoration: underline;
}
.ty-typography del {
  text-decoration: line-through;
}
.ty-typography strong {
  font-weight: 600;
}
.ty-typography i {
  font-style: italic;
}
.ty-typography sup {
  vertical-align: super;
}
.ty-typography sub {
  vertical-align: baseline;
}

h1.ty-typography,
h2.ty-typography,
h3.ty-typography,
h4.ty-typography,
h5.ty-typography,
h6.ty-typography {
  margin-top: 0;
  margin-bottom: 0.5em;
  color: rgba(0, 0, 0, 0.85);
  font-weight: 600;
}

h1.ty-typography {
  font-size: 2.5rem;
  line-height: 1.23;
}

h2.ty-typography {
  font-size: 2rem;
  line-height: 1.35;
}

h3.ty-typography {
  font-size: 1.75rem;
  line-height: 1.35;
}

h4.ty-typography {
  font-size: 1.5rem;
  line-height: 1.4;
}

h5.ty-typography {
  font-size: 1.25rem;
  line-height: 1.2;
}

h6.ty-typography {
  font-size: 1rem;
  line-height: 1.2;
}

p.ty-typography, div.ty-typography {
  margin-top: 0;
  margin-bottom: 1em;
  color: rgba(0, 0, 0, 0.65);
  font-size: 1rem;
  line-height: 1.5;
}

span.ty-typography {
  color: rgba(0, 0, 0, 0.65);
}

@keyframes ty-rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes ty-rotate-reverse {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(-360deg);
  }
}
@keyframes ty-processing {
  0% {
    transform: scale(0.8);
    opacity: 0.5;
  }
  100% {
    transform: scale(2.8);
    opacity: 0;
  }
}
.ty-upload {
  display: inline-block;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: #32325d;
  font-size: 1rem;
  outline: 0;
}
.ty-upload__upload-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #32325d;
  font-size: 1rem;
}
.ty-upload__upload-list-item {
  margin-top: 5px;
  font-size: 1rem;
  transition: background-color 300ms;
}
.ty-upload__upload-list-item:hover {
  background-color: #f5f5f5;
}
.ty-upload__upload-list-item:hover .ty-upload__upload-list-item-status {
  display: none;
}
.ty-upload__upload-list-item:hover .ty-upload__upload-list-item-delete {
  display: inline-block;
}
.ty-upload__upload-list-item-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 4px 4px 4px 2px;
}
.ty-upload__upload-list-item-name {
  flex: 1;
  display: inline-block;
  margin-left: 5px;
  color: #32325d;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.ty-upload__upload-list-item-delete {
  display: none;
  padding-right: 3px;
  cursor: pointer;
}
.ty-upload__upload-list-item-status, .ty-upload__upload-list-item-delete {
  height: 16px;
}
.ty-upload__tip {
  font-size: 12px;
  color: #525f7f;
  margin-top: 7px;
}
.ty-upload__dragger-cover {
  position: relative;
  padding: 15px;
  width: 100%;
  height: 100%;
  text-align: center;
  background: #fafafa;
  border: 1px dashed #d9d9d9;
  border-radius: 2px;
  cursor: pointer;
  transition: border-color 300ms, background-color 300ms;
}
.ty-upload__dragger-cover:hover:not(.ty-upload__dragger-cover_disabled), .ty-upload__dragger-cover_dragover {
  background-color: #efefef;
  border-color: #6e41bf;
}
.ty-upload__dragger-cover_disabled {
  cursor: not-allowed;
}